<template>
  <div>
    <p class="error">{{ error }}</p>
    <div class="mt-3" v-show="false">
      Selected:
      <strong>{{ selected }}</strong>
    </div>
    <b-row>
      <b-col>QR :</b-col>
      <b-col>
        <b>{{ result }}</b>
      </b-col>
    </b-row>
    {{test}}
    <b-button variant="outline-primary" @click="postlogin('001')">test</b-button>
    <b-modal ref="modal" title="ชำระเงิน" @shown="showmodal" @hidden="hiddenmodal">
      <form ref="form" @submit.stop.prevent="handleSubmit">
        <b-form-group
          label-cols="4"
          label-cols-lg="2"
          label-size="sm"
          label="ทั้งหมด"
          label-for="input-sm"
        >
          <b-form-input id="name-input" v-model="total" disabled class="text-right"></b-form-input>
        </b-form-group>
        <b-form-group
          label-cols="4"
          label-cols-lg="2"
          label-size="sm"
          label="รับเงิน"
          label-for="input-sm"
        >
          <b-form-input id="name-input" v-model="receive" ref="receive" class="text-right"></b-form-input>
        </b-form-group>
        <b-form-group
          label-cols="4"
          label-cols-lg="2"
          label-size="sm"
          label="ทอนเงิน"
          label-for="input-sm"
        >
          <b-form-input
            id="name-input"
            v-model="change"
            disabled
            class="text-right"
            style="background-color:white;color:green;font-weight: bold;"
            size="lg"
          ></b-form-input>
        </b-form-group>
      </form>
    </b-modal>
    <qrcode-stream @decode="onDecode" @init="onInit" />
  </div>
</template>

<script>
import { QrcodeStream } from "vue-qrcode-reader";

export default {
  components: { QrcodeStream },

  data() {
    return {
      result: "",
      error: "",
      test: "",
      total: "",
      receive: "",
      change: ""
    };
  },
  watch: {
    receive: {
      handler(val) {
        var change = this.receive - this.total;
        if (change > 0) this.change = change;
      }
    }
  },
  methods: {
    onDecode(result) {
      if (result == "") return;
      this.result = result;
      this.postlogin(result);
    },
    showmodal() {
      this.$refs.receive.$el.focus();
    },
    hiddenmodal() {
      this.$parent.showAlert("finish", "success");
      this.$router.replace({
        name: "home"
      });
    },
    postlogin(qr) {
      const formData = new FormData();
      formData.append("api", "login");
      formData.append("qr", qr);
      axios
        .post(this.$store.state.api, formData)
        .then(response => {
          if (response.data.rows.length > 0) {
            this.$refs.modal.show();
            this.total = response.data.rows[0].total;
          } else {
            this.$parent.showAlert(response.data.data, "success");
            this.$router.replace({
              name: "home"
            });
          }
        })
        .catch(e => {});
    },
    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "NotAllowedError") {
          this.error = "ERROR: you need to grant camera access permisson";
        } else if (error.name === "NotFoundError") {
          this.error = "ERROR: no camera on this device";
        } else if (error.name === "NotSupportedError") {
          this.error = "ERROR: secure context required (HTTPS, localhost)";
        } else if (error.name === "NotReadableError") {
          this.error = "ERROR: is the camera already in use?";
        } else if (error.name === "OverconstrainedError") {
          this.error = "ERROR: installed cameras are not suitable";
        } else if (error.name === "StreamApiNotSupportedError") {
          this.error = "ERROR: Stream API is not supported in this browser";
        }
      }
    }
  }
};
</script>

<style scoped>
.error {
  font-weight: bold;
  color: red;
}
</style>